    text-align: center;
    width: 40%;
    padding: 10px 0px;
    border-radius: 10px;
    background: -webkit-linear-gradient(90deg, #bebce5, #79a0e8);
    background: -o-linear-gradient(90deg, #bebce5, #79a0e8);
    background: -moz-linear-gradient(90deg, #bebce5, #79a0e8);
    background: linear-gradient(90deg, #bebce5, #79a0e8);
    border: none;
    font-size: 12px;
    color: #fff;
  }
  .login-box{
    width: 100%;
    margin: 0 auto;
    background: rgba(255,255,255,0.4);
  }
  .box-tab{
    width: 100%;
    margin: 10px auto;
  }
  .box-tab .user,.box-tab .password{
    width: 100%;
    margin: 0 auto;
    padding-top: 25px;
  }
  .user i,.password i{
    display: inline-block;
    vertical-align: middle;
    width: 45px;
    height: 35px;
    margin: 0 auto;
    text-align: center;
    font-size: 12px;
    color: #fff;
    line-height: 35px;
    border-top-left-radius: 7px;
    border-bottom-left-radius: 7px;
    padding: 0px 0px;
    margin-left: 15%;
    //top: 5px;
    //left: 4px;
    background: -webkit-linear-gradient(90deg, #bebce5, #79a0e8);
    background: -o-linear-gradient(90deg, #bebce5, #79a0e8);
    background: -moz-linear-gradient(90deg, #bebce5, #79a0e8);
    background: linear-gradient(90deg, #bebce5, #79a0e8);
  }

  .user input,.password input{
    width: 60%;
    padding: 2px 4.5px 4.5px 4.5px;
    margin-left: -5px;
  }
  .fot{
    display: block;
    margin:0 auto;
    text-align: center;
    color: #999;
    letter-spacing: 2px;
    margin-bottom: 20px;
    font-size: 12px;
  }
  .login-footer{
    width: 100%;
    margin: 20px auto;
  }
  .login-footer span{
    display: block;
    margin: 0 auto;
    text-align: center;
    font-size: 12px;
    color: #999;
    margin-top: -20px;
  }
  .login-footer i{
    display: inline-block;
  }
}
}









